// about-us-button animation 
.about-us-button1{
    @extend .col-sm-6;
    width: 173.2px;
    height: 34.7px;
    border:1px solid $orange;
    border-radius:9px;
    float:left;
    text-align:center;
    cursor:pointer; 
    position:relative;
    box-sizing:border-box;
    overflow:hidden;
    margin:20px auto;
    background:$orange;
    a{
      color: $orange;
  }
  }
   
  .about-us-button1 a{
    font-size:17.9px;
    font-family: $sc-font-family;
    color:#fff;
    text-decoration:none;
    transition:all .4s ease;
    z-index:2;
    position:relative;
    font-weight: lighter;
    line-height: 1.8;
  }
  .about-us-eff-1{
    width: 100%;
    height:50px;
    left: -100%;
    background:#fff;
    position:absolute;
    transition:all .4s ease;
    z-index:1;
  }
  .about-us-button1:hover .about-us-eff-1{
    left:0;
  }
  .about-us-button1 .anchor1{
    color:#fff;
}
  .about-us-button1:hover a{
    color:$orange;
  }
  
  @media (max-width:$screen-sm-min) {
    .about-us-video-cms-area{
      width: 100%;
    }
    .about-us-video-text-cms-area{
      width: auto;
    }
  }
   // only hidden in  mobiles  $screen-sm-min
  @media (max-width: 767px) {
    .about-us-button1{
        display: none;
    }
    .about-us-button3{
      display: none;
    }
    .about-us-eff-1{
        width: 100%;
        left: -100%;
    }
}
 

 

@media (min-width: $screen-sm-min) {
  .about-us-video-cms-area{
    width: 50%;
  }
  .about-us-video-text-cms-area{
    width: 40%;
  }
}
  
