.about-us-graph-main-content{
 
  @media (max-width:767px){
    display: none;
  }
  @media (min-width:$screen-sm-min){
    .content-1{
      padding: 0;
      height: 300px;
    }
    .img-section-content{
      height: 100px;
      position: relative;
      img{
        @media (min-width:$screen-sm-min)and(max-width:882px){
         padding: 3%;
        }
        @media (min-width:882px) and (max-width:1200px){
        padding: 3%;
        }
        position: absolute;
      }
    }
    .content-img-1{
      img{
         bottom: 20px;
      }
    }
    .content-img-2{
      img{
         bottom: 0;
      }
    }
    .content-img-3{
      img{
         bottom: 20px;
      }
    }
    .content-img-4{
      img{
         bottom: 15px;
      }
    }
    .content-img-5{
      img{
         bottom: 30px;
      }
    }
    .content-img-6{
      img{
         bottom: 15px;
      }
    }
    .line-content{
      margin-bottom: 10px;
      .circle {
        height: 15px;
        width:15px;
        background-color: white;
        border:1px solid  #f58331;
        border-radius: 50%;
        float: left;
        margin: 0px;
        padding: 0px;
      }
      .line{
        @media (min-width:$screen-sm-min)and(max-width:882px){
          width: 87.8%;
        }
        @media (min-width:882px) and (max-width:1200px){
         width: 89.9%;
        }
        @media (min-width:1200px){
  
          width:91.8%;
        }
        height: 2px;
        background-color: #f58331;
        float: left;
        margin:7px 0px;
        padding: 0px;
        display: inline-block;
        // display: ;
      }
    }
    .text-graph-content{
      // height: 100px;
      padding-right: 4%;
      padding-left: 4%;
      .graph-date-content{
        font-weight: 600;
        margin-bottom: 10px;
      }
      // .graph-text-content{
  
      // }
    }
  }
 
}
 